import React, { createContext, ReactNode } from 'react';
import { AriaRadioGroupProps } from 'react-aria';
import { RadioGroupState, useRadioGroupState } from 'react-stately';

import { useRadioGroup } from '../_hooks/RadioGroup/useRadioGroup';
import { Stack } from '../Stack';
import { Text } from '../Typography';

import styles from './RadioGroup.module.scss';

export interface RadioGroupProps
    extends Omit<AriaRadioGroupProps, 'errorMessage'> {
    children: ReactNode;
    errorMessage?: string;
    labelSuffix?: string;
    tooltipTrigger?: React.ReactNode;
}

export const RadioContext = createContext<RadioGroupState | null>(null);

const RadioGroup = (props: RadioGroupProps) => {
    const state = useRadioGroupState(props);
    const {
        radioGroupProps,
        isInvalid,
        errorMessageProps,
        validationErrors,
        labelProps,
    } = useRadioGroup(props, state);

    const {
        children,
        errorMessage: customErrorMessage,
        validationBehavior,
        label,
        labelSuffix,
        tooltipTrigger,
    } = props;

    // Allow only native error messages with native validation behaviour
    const errorMessage =
        validationBehavior === 'native'
            ? validationErrors.join(' ')
            : customErrorMessage;

    return (
        <div {...radioGroupProps}>
            <Stack>
                {label && (
                    <div className={styles.label__container}>
                        {/* Radio group should not have a label element officially */}
                        <span className={styles.label} {...labelProps}>
                            {label}
                            {labelSuffix && (
                                <>
                                    {/* Use an extra element as separator so everything stays inline and no extra margin on either side needed, which doesn't look right when text starts wrapping */}
                                    <span
                                        className={
                                            styles['label__suffix-separator']
                                        }
                                    ></span>
                                    <Text
                                        variant="line"
                                        italic
                                        size="small"
                                        className={styles.label__suffix}
                                    >
                                        {labelSuffix}
                                    </Text>
                                </>
                            )}
                        </span>
                        {tooltipTrigger}
                    </div>
                )}

                <RadioContext.Provider value={state}>
                    {children}
                </RadioContext.Provider>

                {isInvalid && errorMessage && (
                    <div
                        {...errorMessageProps}
                        className={styles['message--error']}
                    >
                        <Text variant="line" size="small">
                            {errorMessage}
                        </Text>
                    </div>
                )}
            </Stack>
        </div>
    );
};

export default RadioGroup;
