import React, { forwardRef, ReactNode, useContext, useRef } from 'react';
import { AriaRadioProps, useFocusRing, VisuallyHidden } from 'react-aria';
import type { RadioGroupState } from 'react-stately';
import { mergeProps, useObjectRef } from '@react-aria/utils';
import classNames from 'classnames';

import { useRadio } from '../_hooks/RadioGroup/useRadio';
import { RadioContext } from '../RadioGroup/RadioGroup';
import { Stack } from '../Stack';
import { Label, Text } from '../Typography';

import styles from './Choice.module.scss';

export interface ChoiceProps extends AriaRadioProps {
    children?: ReactNode;
    label: string;
    description?: string;
    choiceType?: 'simple' | 'complex';
    layout?: 'row' | 'column';
}

const Choice = forwardRef<HTMLInputElement, ChoiceProps>((props, ref) => {
    const forwardedRef = useObjectRef(ref);
    const fallbackRef = useRef(null);
    const currentRef = forwardedRef || fallbackRef;
    const {
        children,
        label,
        description,
        choiceType = 'simple',
        layout = 'row',
    } = props;

    const state = useContext(RadioContext);

    const { inputProps, isSelected, isPressed, labelProps } = useRadio(
        props,
        state as RadioGroupState,
        currentRef
    );
    const { isFocusVisible, focusProps } = useFocusRing();

    const choiceClassList = classNames(styles.choice, {
        [styles[`choice--pressed`]]: isPressed,
        [styles[`choice--selected`]]: isSelected,
        [styles[`choice--focused`]]: isFocusVisible,
        [styles[`choice--invalid`]]: state?.isInvalid,
    });

    const layoutClassList = classNames({
        [styles['column']]: layout === 'column',
        [styles['row']]: layout === 'row',
        [styles[`simple`]]: choiceType === 'simple',
        [styles[`complex`]]: choiceType === 'complex',
    });

    return (
        <Label
            className={classNames(choiceClassList, layoutClassList)}
            {...labelProps}
        >
            <span className={styles.choice__indicator} aria-hidden="true">
                {/* TODO: replace with icon from library when it is added */}
                <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    xmlns="http://www.w3.org/2000/svg"
                    className={styles.choice__icon}
                >
                    <circle cx="8" cy="8" r="5" />
                </svg>
            </span>

            <VisuallyHidden>
                <input
                    {...mergeProps(inputProps, focusProps)}
                    ref={currentRef}
                />
            </VisuallyHidden>

            <div className={styles.choice__content}>
                <Stack>
                    {choiceType === 'simple' ? (
                        <div className={styles.choice__title}>{label}</div>
                    ) : (
                        <>
                            <div className={styles.choice__title}>{label}</div>

                            {description && (
                                <Text
                                    variant="line"
                                    size="small"
                                    weight="400"
                                    className={styles.choice__description}
                                >
                                    {description}
                                </Text>
                            )}
                            {children && <div>{children}</div>}
                        </>
                    )}
                </Stack>
            </div>
        </Label>
    );
});

Choice.displayName = 'Choice';

export default Choice;
